export default {
  name: "app-input",
  components: {},
  props: ["value", "store"],
  data() {
    return {};
  },
  computed: {},
  mounted() {},
  methods: {
    onInput(event) {
      this.store && localStorage.setItem(this.store, this.value);
      this.$emit("input", event.target.value);
    },
  },
};
